.App {
  height: 100vh;
  user-select: none;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.ReactModal__Overlay {
  opacity: 0;
  transition: opacity 200ms ease-in-out;
}

.ReactModal__Overlay--after-open{
  opacity: 1;
}

.ReactModal__Overlay--before-close{
  opacity: 0;
}